import React from 'react'
import Layout from '../../containers/Layout'
import H2 from '../../components/Headings/H2'
import H4 from '../../components/Headings/H4'
import Lead from '../../components/Text/Lead'
import P from '../../components/Text/P'
import CodeCard from '../../components/CodeCard'
import ExampleSection from '../../components/ExampleSection'

const buttonSizesCode = `
<button class="rounded-lg px-6 h-12 text-lg text-indigo-100 bg-indigo-700 focus:shadow-outline hover:bg-indigo-600 transition-colors duration-150">Large</button>
<button class="rounded-lg px-5 h-10 text-indigo-100 bg-indigo-700 focus:shadow-outline hover:bg-indigo-600 transition-colors duration-150">Regular</button>
<button class="rounded-lg px-4 h-8 text-sm text-indigo-100 bg-indigo-700 focus:shadow-outline hover:bg-indigo-600 transition-colors duration-150">Small</button>
<a class="rounded-lg px-4 h-8 text-sm text-indigo-100 bg-indigo-700 focus:shadow-outline hover:bg-indigo-600 transition-colors duration-150 inline-flex items-center" href="#">Link</a>
<input class="rounded-lg px-4 h-8 text-sm text-indigo-100 bg-indigo-700 focus:shadow-outline hover:bg-indigo-600 transition-colors duration-150 cursor-pointer" type="submit" value="Input">
`

function Buttons() {
  return (
    <Layout>
      <H2>Buttons</H2>
      <Lead>Button components using Tailwind CSS to speed up your project.</Lead>
      <hr className="mb-3" />

      <ExampleSection id="button-sizes">
        <H4>Button sizes</H4>
        <P>Different sizes of a button component.</P>
        <CodeCard lang="html" code={buttonSizesCode} />
      </ExampleSection>
    </Layout>
  )
}

export default Buttons
